import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";


import "../tailwind.generated.css";

import { Page } from "../enums/Page.tsx"
import { Home } from "./home/Home";
import { Lobby } from "./lobby/Lobby";
import { Prepare } from "./prepare/Prepare";
import { Game } from "./game/Game";
import { startListening } from "../state/messages/actions";


export const App = () => {
    const dispatch = useDispatch();
    const [page, setPage] = useState(Page.MAIN_PAGE);

    useEffect(() => {
        dispatch(startListening());
    }, [dispatch]);

    switch (page) {
        case Page.MAIN_PAGE:
            return <Home onPageChange={setPage}/>;
        case Page.WAITING_FOR_SECOND_PLAYER:
            return <Lobby onPageChange={setPage}/>;
        case Page.PREPARE_GAME:
            return <Prepare onPageChange={setPage}/>;
        case Page.IN_GAME:
            return <Game onPageChange={setPage}/>;
        default:
            return <Home onPageChange={setPage}/>;
    }
}
