import { examplePrepareState } from "../../domain/examplePrepareState";
import { Team } from "../../enums/Team";
import {
    PREPARE_STATE_ACTIVATE_UNIT,
    PREPARE_STATE_MOVE_SELECTED_UNIT_ON_BOARD,
    PREPARE_STATE_MOVE_SELECTED_UNIT_ON_HAND,
    PREPARE_STATE_NEW_GAME
} from "./actions";

const getNewGameState = (player, boardSize, units) => {
    const availableRows = player === Team.RED ? [boardSize - 2, boardSize - 1] : [0, 1];
    return {
        player: player,
        boardSize: boardSize,
        board: Array(boardSize ** 2).fill().map((_, i) => ({
            id: i,
            position: { x: i % boardSize, y: Math.floor(i / boardSize) },
            isAvailable: availableRows.includes(Math.floor(i / boardSize))
        })),
        hand: Array(units.reduce((a, c) => c.amount + a, 0)).fill().map((_, i) => ({
            id: 1000 + i,
            position: { x: i % boardSize, y: Math.floor(i / boardSize) },
        })),
        units: units.map(u => Array(u.amount).fill({
            team: player,
            unit: u.unit,
            isSelected: false,
            isInHand: true
        })).flat().map((u, i) => ({
            ...u,
            id: 2000 + i,
            position: { x: i % boardSize, y: Math.floor(i / boardSize) },
        })),
        availableRows: availableRows
    };
};

const initialState = getNewGameState(examplePrepareState.player, examplePrepareState.boardSize, examplePrepareState.availableUnits);

export const prepareStateReducer = (state = initialState, action) => {
    const isCellAvailable = (id) => {
        const cell = state.board.find(c => c.id === id);
        const unitOnCell = state.units.find(u => !u.isInHand && u.position && u.position.x === cell.position.x && u.position.y === cell.position.y);
        return state.availableRows.includes(cell.position.y) && !unitOnCell;
    }

    const { type, payload } = action;

    if (type === PREPARE_STATE_ACTIVATE_UNIT) {
        return {
            ...state,
            units: state.units.map(u => ({ ...u, isSelected: u.id === payload.id && !u.isSelected }))
        };
    }

    if (type === PREPARE_STATE_MOVE_SELECTED_UNIT_ON_BOARD) {
        const cell = state.board.find(c => c.id === payload.id);
        const selectedUnit = state.units.find(u => u.isSelected);
        if (!cell.isAvailable || !selectedUnit) {
            return state;
        }
        state.units = state.units.map(u => u.id === selectedUnit.id ? { ...u, isSelected: false, position: cell.position, isInHand: false } : u);
        state.board = state.board.map(c => ({ ...c, isAvailable: isCellAvailable(c.id) }));
        return {
            ...state
        };
    }

    if (type === PREPARE_STATE_MOVE_SELECTED_UNIT_ON_HAND) {
        const cell = state.hand.find(c => c.id === payload.id);
        const selectedUnit = state.units.find(u => u.isSelected);
        if (!selectedUnit) {
            return state;
        }
        state.units = state.units.map(u => u.id === selectedUnit.id ? { ...u, isSelected: false, position: cell.position, isInHand: true } : u);
        state.board = state.board.map(c => ({ ...c, isAvailable: isCellAvailable(c.id) }));
        return {
            ...state
        };
    }

    if (type === PREPARE_STATE_NEW_GAME) {
        state = getNewGameState(payload.player, examplePrepareState.boardSize, examplePrepareState.availableUnits);
        return state;
    }

    return state;
};
