import io from "socket.io-client";

import {
    gameStateSetRoomId,
    gameStateSetPlayer,
    gameStateUpdateState
} from "../../state/gameState/actions";

let socket = null;

export const startListening = () => (dispatch) => {
    socket = io("http://webprogramozas.inf.elte.hu:3030");

    socket.on("room-is-full", msg => {
        console.log("room-is-full", msg);
        dispatch(gameStateSetRoomId(msg.roomId));
        dispatch(gameStateSetPlayer(msg.player));
    });

    socket.on("player-joined", msg => {
        console.log("player-joined", msg);
    });

    socket.on("state-changed", msg => {
        dispatch(gameStateUpdateState(msg.state));
        console.log("state-changed", msg);
    });

    socket.on("action-sent", msg => {
        console.log("player-left", msg);
    });

    socket.on("player-left", msg => {
        console.log("player-left", msg);
    });
};

export const createRoom = () => (dispatch) => {
    socket.emit("create-room", ack => {
        if (ack.status === 'ok') {
            dispatch(gameStateSetRoomId(ack.roomId));
        } else {
            console.error(`error while creating room: ${ack.message}`);
        }
    });
};

export const joinRoom = (roomId) => (dispatch) => {
    socket.emit("join-room", roomId, ack => {
        if (ack.status !== 'ok') {
            console.error(`error while joining room: ${ack.message}`);
        }
    });
};

export const syncState = (gameState, broadcast = true) => (dispatch) => {
    socket.emit("sync-state", gameState.roomId, gameState, broadcast, ack => {
        if (ack.status !== 'ok') {
            console.error(`error while syncing state: ${ack.message}`);
        }
    });
};

export const leaveRoom = (roomId) => (dispatch) => {
    socket.emit("leave-room", roomId, ack => {
        if (ack.status !== 'ok') {
            console.error(`error while leaving room: ${ack.message}`);
        }
    });
};
