import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

import { createRoom, leaveRoom } from "../../state/messages/actions";
import { getGameState } from "../../state/gameState/selectors";
import { Page } from "../../enums/Page.tsx";
import "./loading.css";

export const Lobby = ({ onPageChange }) => {
    const dispatch = useDispatch();
    const gameState = useSelector(getGameState);

    const onMenuButtonClick = () => {
        dispatch(leaveRoom(gameState.roomId));
        onPageChange(Page.MAIN_PAGE);
    }

    useEffect(() => {
        if (!gameState.roomId) {
            dispatch(createRoom());
        }
        if (gameState.player && gameState.units.length === 0) {
            onPageChange(Page.PREPARE_GAME);
        }
        if (gameState.currentPlayer) {
            onPageChange(Page.IN_GAME);
        }
    });

    return (
        <div className="flex flex-col h-screen bg-gray-800">
            <div className="flex flex-col m-auto items-center">
                <div className="lds-ellipsis mt-64"><div></div><div></div><div></div><div></div></div>
                <div className="text-white">
                    Vrakozs az ellenflre ...
                </div>
                {(!gameState.player) &&
                    <div className="text-white">
                        Szoba azonost: {gameState.roomId}
                    </div>
                }
            </div>
            <div className="flex justify-center mb-32">
                <button className="mt-20 bg-transparent hover:bg-yellow-700 text-yellow-600 font-bold hover:text-white py-2 px-4 border border-yellow-600 hover:border-transparent rounded"
                        onClick={onMenuButtonClick}>
                    Men
                </button>
            </div>
        </div>
    );
};

Lobby.propTypes = {
    onPageChange: PropTypes.func.isRequired
}
