export const GAME_STATE_NEW_GAME = "GAME_STATE_NEW_GAME";
export const GAME_STATE_MOVE_SELECTED_UNIT_ON_BOARD = "GAME_STATE_MOVE_SELECTED_UNIT_ON_BOARD";
export const GAME_STATE_HANDLE_UNIT_CLICK = "GAME_STATE_HANDLE_UNIT_CLICK";
export const GAME_STATE_CONCLUDE_FIGHT = "GAME_STATE_CONCLUDE_FIGHT";
export const GAME_STATE_SET_ROOM_ID = "GAME_STATE_SET_ROOM_ID";
export const GAME_STATE_SET_PLAYER = "GAME_STATE_SET_PLAYER";
export const GAME_STATE_ADD_UNITS = "GAME_STATE_ADD_UNITS";
export const GAME_STATE_UPDATE_STATE = "GAME_STATE_UPDATE_STATE";

export const gameStateNewGame = (boardSize, units) => ({
    type: GAME_STATE_NEW_GAME,
    payload: { boardSize: boardSize, units: units }
});

export const gameStateMoveSelectedUnitOnBoard = (cellId) => ({
    type: GAME_STATE_MOVE_SELECTED_UNIT_ON_BOARD,
    payload: { id: cellId }
});

export const gameStateHandleUnitClick = (unitId) => ({
    type: GAME_STATE_HANDLE_UNIT_CLICK,
    payload: { id: unitId }
});

export const gameStateConcludeFight = () => ({
    type: GAME_STATE_CONCLUDE_FIGHT,
    payload: null
});

export const gameStateSetRoomId = (roomId) => ({
    type: GAME_STATE_SET_ROOM_ID,
    payload: { id: roomId }
});

export const gameStateSetPlayer = (player) => ({
    type: GAME_STATE_SET_PLAYER,
    payload: { player: player }
});

export const gameStateAddUnits = (units) => ({
    type: GAME_STATE_ADD_UNITS,
    payload: { units: units }
});

export const gameStateUpdateState = (state) => ({
    type: GAME_STATE_UPDATE_STATE,
    payload: { state: state }
});
