import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

import { syncState } from "../../state/messages/actions";
import { getGameState } from "../../state/gameState/selectors";
import {
    prepareStateActivateUnit,
    prepareStateMoveSelectedUnitOnBoard,
    prepareStateMoveSelectedUnitOnHand
} from "../../state/prepareState/actions";
import { gameStateAddUnits } from "../../state/gameState/actions";
import { getPrepareState } from "../../state/prepareState/selectors";
import { Page } from "../../enums/Page";
import { Board } from "./Board";


export const Prepare = ({ onPageChange }) => {
    const gameState = useSelector(getGameState);
    const prepareState = useSelector(getPrepareState);
    const dispatch = useDispatch();

    const handleUnitClick = (unitId) => dispatch(prepareStateActivateUnit(unitId));
    const handleBoardClick = (boardCellId) => dispatch(prepareStateMoveSelectedUnitOnBoard(boardCellId));
    const handleHandClick = (handCellId) => dispatch(prepareStateMoveSelectedUnitOnHand(handCellId));

    const handleReady = () => {
        if (prepareState.units.some(u => u.isInHand)) {
            return;
        }
        dispatch(gameStateAddUnits(prepareState.units));
    }

    useEffect(() => {
        if (gameState.player && gameState.units.filter(u => u.team === gameState.player).length > 0) {
            dispatch(syncState(gameState));
            onPageChange(Page.WAITING_FOR_SECOND_PLAYER);
        }
    });

    return (
        <div className="flex h-screen bg-gray-800">
            <div className="flex flex-col m-auto items-center">
                <Board
                    gameState={prepareState}
                    onUnitClick={handleUnitClick}
                    onBoardClick={handleBoardClick}
                    onHandClick={handleHandClick}
                    onReady={handleReady}
                />
            </div>
        </div>
    );
};

Prepare.propTypes = {
    onPageChange: PropTypes.func.isRequired
}
