import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

import { prepareStateNewGame } from "../../state/prepareState/actions";
import { joinRoom } from "../../state/messages/actions";
import { getGameState } from "../../state/gameState/selectors";
import { Page } from "../../enums/Page.tsx"
import logo from "../../assets/logo.png";

export const Home = ({ onPageChange }) => {
    const dispatch = useDispatch();
    const gameState = useSelector(getGameState);
    const [roomId, setRoomId] = useState("");

    const onConnectButtonClick = () => {
        if (roomId === "") {
            return;
        }
        dispatch(joinRoom(roomId));
    }

    useEffect(() => {
        if (gameState.player && gameState.units.length === 0) {
            dispatch(prepareStateNewGame(gameState.player));
            onPageChange(Page.PREPARE_GAME);
        }
    });

    return (
        <div className="flex h-screen bg-gray-800">
            <div className="flex flex-col m-auto" style={{width: 330}}>
                <img src={logo} alt="" />
                <button className="mx-4 mt-6 bg-yellow-600 hover:bg-yellow-700 text-white font-bold text-xl py-2 px-4 rounded"
                        onClick={() => onPageChange(Page.WAITING_FOR_SECOND_PLAYER)}>
                    j jtk
                </button>
                <div className="mx-4 mt-2 flex flex-row">
                    <input className="w-1/2 mr-2 bg-transparent focus:bg-gray-700 border border-yellow-600 placeholder-yellow-700 focus:placeholder-yellow-500 text-yellow-600 font-bold rounded py-2 px-4 block appearance-none leading-normal"
                           placeholder="azonosit"
                           onInput={e => setRoomId(e.target.value)} />
                    <button className={`bg-yellow-600 text-white font-bold text-xl py-2 px-4 rounded ${roomId ? "hover:bg-yellow-700" : "opacity-50 cursor-not-allowed"}`}
                            style={roomId ? {} : {outline: "none"}}
                            onClick={onConnectButtonClick}>
                        Csatlakozs
                    </button>
                </div>
                <button className="mx-4 mt-2 bg-yellow-600 hover:bg-yellow-700 text-white font-bold text-xl py-2 px-4 rounded"
                        onClick={() => window.location.href = "http://www.ketaklub.hu/letoltes/Stratego%20Aoriginal%20Piatnik.pdf"}>
                    Szablyok
                </button>
            </div>
        </div>
    );
};

Home.propTypes = {
    onPageChange: PropTypes.func.isRequired
}
