import React from "react";
import PropTypes from "prop-types";

import { Team } from "../../enums/Team";
import { Unit } from "../../enums/Unit";

export const Board = ({ gameState, onUnitClick, onBoardClick, onHandClick, onReady }) => {
    return (
        <div className="flex flex-col items-center">
            <div className="text-white mb-6">
                Elkszls - {gameState.player === Team.RED ? "Piros" : "Kk"} csapat
            </div>
            <div
                className="relative"
                style={{width: `${gameState.boardSize * 3}rem`, height: `${(gameState.boardSize + Math.ceil(gameState.units.length / gameState.boardSize)) * 3 + 4}rem`}}
            >
                {gameState.board.map(cell =>
                    <div
                        className="absolute z-0 w-12 h-12 select-none flex items-center justify-center"
                        style={{left: `${cell.position.x * 3}rem`, top: `${cell.position.y * 3}rem`}}
                        onClick={() => onBoardClick(cell.id)}
                        key={cell.id}
                    >
                        <div className={`bg-${cell.isAvailable ? "green" : "gray"}-500 w-10 h-10 rounded flex items-center justify-center transition duration-300 ease-in-out`}></div>
                    </div>
                )}
                <div
                    className="absolute text-white text-sm text-center"
                    style={{width: `${gameState.boardSize * 3}rem`, top: `${gameState.boardSize * 3 + 2}rem`}}
                >
                    Elrhet egysgek
                </div>
                {gameState.hand.map(cell =>
                    <div
                        className="absolute z-0 w-12 h-12 select-none flex items-center justify-center"
                        style={{left: `${cell.position.x * 3}rem`, top: `${cell.position.y * 3 + gameState.boardSize * 3 + 4}rem`}}
                        onClick={() => onHandClick(cell.id)}
                        key={cell.id}
                    >
                        <div className="bg-transparent w-10 h-10 rounded flex items-center justify-center transition duration-300 ease-in-out border-solid border-2 border-gray-500"></div>
                    </div>
                )}
                {gameState.units.map(unit =>
                    <div
                        className="absolute z-10 w-12 h-12 select-none flex items-center justify-center transition-all duration-300 ease-in-out"
                        style={{left: `${unit.position.x * 3}rem`, top: `${unit.position.y * 3 + (unit.isInHand ? gameState.boardSize * 3 + 4 : 0)}rem`}}
                        onClick={() => onUnitClick(unit.id)}
                        key={unit.id}
                    >
                        <div className={`bg-${unit.team}-${unit.isSelected ? 300 : 500} w-10 h-10 rounded flex items-center justify-center`}>
                            {unit.unit === Unit.BOMB ? "" : unit.unit === Unit.FLAG ? "" : unit.unit}
                        </div>
                    </div>
                )}
                <div
                    className="absolute flex items-center justify-center"
                    style={{left: `${gameState.boardSize * 3 + 2}rem`, top: `${(gameState.boardSize + Math.ceil(gameState.units.length / gameState.boardSize) / 2) * 3 + 2.5}rem`}}
                >
                    <button
                        className={`bg-yellow-600 text-white font-bold text-xl py-2 px-4 rounded ${gameState.units.some(u => u.isInHand) ? "opacity-50 cursor-not-allowed" : "hover:bg-yellow-700"}`}
                        style={gameState.units.some(u => u.isInHand) ? {outline: "none"} : {}}
                        onClick={onReady}
                    >
                        Tovbb
                    </button>
                </div>
            </div>
        </div>
    );
}

Board.propTypes = {
    gameState: PropTypes.object.isRequired,
    onUnitClick: PropTypes.func.isRequired,
    onBoardClick: PropTypes.func.isRequired,
    onHandClick: PropTypes.func.isRequired,
    onReady: PropTypes.func.isRequired
}
