import React from "react";
import PropTypes from "prop-types";

import { Team } from "../../enums/Team";
import { Unit } from "../../enums/Unit";

export const Board = ({ gameState, onUnitClick, onBoardClick, onMenuButtonClick }) => {
    return (
        <>
            <div className="text-white mb-6">
                {!gameState.winner && `${gameState.currentPlayer === Team.RED ? "Piros" : "Kk"} jtkos kvetkezik`}
                {gameState.winner === Team.RED && "Piros jtkos gyztt!"}
                {gameState.winner === Team.BLUE && "Kk jtkos gyztt!"}
                {gameState.winner === Team.NONE && "Dntetlen!"}
            </div>
            <div
                className="relative"
                style={{width: `${gameState.boardSize * 3}rem`, height: `${gameState.boardSize * 3}rem`}}
            >
                {gameState.board.map(cell =>
                    <div
                        className="absolute z-0 w-12 h-12 select-none flex items-center justify-center"
                        style={{left: `${cell.position.x * 3}rem`, top: `${cell.position.y * 3}rem`}}
                        onClick={() => onBoardClick(cell.id)}
                        key={cell.id}
                    >
                        <div className={`bg-${cell.isAvailable && !gameState.isFightInProgress ? "green" : "gray"}-500 w-10 h-10 rounded flex items-center justify-center`}></div>
                    </div>
                )}
                {gameState.graveyardBlue.map(cell =>
                    <div
                        className="absolute z-0 w-12 h-12 select-none flex items-center justify-center"
                        style={{left: `${-(Math.ceil(gameState.graveyardBlue.length / gameState.boardSize) - cell.position.x) * 3 - 2}rem`, top: `${cell.position.y * 3}rem`}}
                        key={cell.id}
                    >
                        <div className="bg-blue-800 bg-opacity-25 w-10 h-10 rounded flex items-center justify-center transition duration-300 ease-in-out border-solid border-2 border-gray-500"></div>
                    </div>
                )}
                {gameState.graveyardRed.map(cell =>
                    <div
                        className="absolute z-0 w-12 h-12 select-none flex items-center justify-center"
                        style={{left: `${(gameState.boardSize + cell.position.x) * 3 + 2}rem`, top: `${cell.position.y * 3}rem`}}
                        key={cell.id}
                    >
                        <div className="bg-red-800 bg-opacity-25 w-10 h-10 rounded flex items-center justify-center transition duration-300 ease-in-out border-solid border-2 border-gray-500"></div>
                    </div>
                )}
                {gameState.units.map(unit =>
                    <div
                        className={`absolute z-10 w-12 h-12 select-none flex items-center justify-center ${unit.isAlive ? "transition-all duration-300 ease-in-out" : ""}`}
                        style={{
                            left: `${
                                unit.team === Team.RED && !unit.isAlive ? (gameState.boardSize + unit.position.x) * 3 + 2 :
                                unit.team === Team.BLUE && !unit.isAlive ? -(Math.ceil(gameState.graveyardBlue.length / gameState.boardSize) - unit.position.x) * 3 - 2 :
                                unit.position.x * 3
                            }rem`,
                            top: `${unit.position.y * 3}rem`
                        }}
                        onClick={() => onUnitClick(unit.id)}
                        key={unit.id}
                    >
                        <div className={`bg-${unit.team}-${unit.id === gameState.selectedUnitId ? 300 : 500} w-10 h-10 rounded flex items-center justify-center
                                       ${!gameState.isFightInProgress && unit.isAttackable ? "border-solid border-2 border-yellow-400 border-opacity-75" : ""}
                                       ${gameState.isFightInProgress && unit.id === gameState.attackedUnitId ? `border-solid border-2 border-${unit.team === Team.RED ? Team.BLUE : Team.RED}-400 border-opacity-75` : ""}`}
                        >
                            {
                                !gameState.winner && unit.isAlive && unit.team !== gameState.player && gameState.attackedUnitId !== unit.id && gameState.selectedUnitId !== unit.id ? "?" :
                                unit.unit === Unit.BOMB ? "" :
                                unit.unit === Unit.FLAG ? "" :
                                unit.unit
                            }
                        </div>
                    </div>
                )}
            </div>
            {gameState.winner &&
                <button
                    className="bg-yellow-600 text-white font-bold text-xl py-2 px-4 rounded hover:bg-yellow-700 mt-6"
                    style={gameState.units.some(u => u.isInHand) ? {outline: "none"} : {}}
                    onClick={onMenuButtonClick}
                >
                    Men
                </button>
            }
        </>
    );
}

Board.propTypes = {
    gameState: PropTypes.object.isRequired,
    onUnitClick: PropTypes.func.isRequired,
    onBoardClick: PropTypes.func.isRequired,
    onMenuButtonClick: PropTypes.func.isRequired
}
