import { exampleGameState } from "../../domain/exampleGameState";
import { Team } from "../../enums/Team";
import { Unit } from "../../enums/Unit";
import {
    GAME_STATE_NEW_GAME,
    GAME_STATE_MOVE_SELECTED_UNIT_ON_BOARD,
    GAME_STATE_HANDLE_UNIT_CLICK,
    GAME_STATE_CONCLUDE_FIGHT,
    GAME_STATE_SET_ROOM_ID,
    GAME_STATE_SET_PLAYER,
    GAME_STATE_ADD_UNITS,
    GAME_STATE_UPDATE_STATE
} from "./actions";

const getNewGameState = (boardSize, units) => {
    return {
        roomId: null,
        player: null,
        currentPlayer: null,
        winner: null,
        boardSize: boardSize,
        isFightInProgress: false,
        selectedUnitId: null,
        attackedUnitId: null,
        synced: true,
        board: Array(boardSize ** 2).fill().map((_, i) => ({
            id: i,
            position: { x: i % boardSize, y: Math.floor(i / boardSize) },
            isAvailable: false
        })),
        units: units.map((u, i) => ({
            id: 1000 + i,
            team: u.team,
            unit: u.unit,
            position: u.position,
            isAttackable: false,
            isAlive: true,
        })),
        graveyardRed: Array(units.filter(u => u.team === Team.RED).length).fill().map((_, i) => ({
            id: 2000 + i,
            position: {
                x: i % Math.ceil(units.filter(u => u.team === Team.RED).length / boardSize),
                y: Math.floor(i / Math.ceil(units.filter(u => u.team === Team.RED).length / boardSize))
            }
        })),
        graveyardBlue: Array(units.filter(u => u.team === Team.BLUE).length).fill().map((_, i) => ({
            id: 3000 + i,
            position: {
                x: i % Math.ceil(units.filter(u => u.team === Team.BLUE).length / boardSize),
                y: Math.floor(i / Math.ceil(units.filter(u => u.team === Team.BLUE).length / boardSize))
            }
        }))
    };
}

const initialState = getNewGameState(exampleGameState.boardSize, exampleGameState.units);

export const gameStateReducer = (state = initialState, action) => {
    const setNewGameState = (boardSize, units) => {
        state = getNewGameState(boardSize, units);
    }

    const isCellAvailable = (cellId, unitId) => {
        const cell = state.board.find(c => c.id === cellId);
        const unit = state.units.find(u => u.id === unitId);
        if (!cell || !unit) {
            return false;
        }
        if (unit.unit === Unit.FLAG || unit.unit === Unit.BOMB) {
            return false;
        }
        if (unit.unit === Unit.SCOUT) {
            return (unit.position.x === cell.position.x && cell.position.y > unit.position.y &&
                    state.units.filter(u => u.isAlive && u.position.x === cell.position.x && u.position.y < cell.position.y && u.position.y > unit.position.y).length === 0) ||
                   (unit.position.x === cell.position.x && cell.position.y < unit.position.y &&
                    state.units.filter(u => u.isAlive && u.position.x === cell.position.x && u.position.y > cell.position.y && u.position.y < unit.position.y).length === 0) ||
                   (unit.position.y === cell.position.y && cell.position.x > unit.position.x &&
                    state.units.filter(u => u.isAlive && u.position.y === cell.position.y && u.position.x < cell.position.x && u.position.x > unit.position.x).length === 0) ||
                   (unit.position.y === cell.position.y && cell.position.x < unit.position.x &&
                    state.units.filter(u => u.isAlive && u.position.y === cell.position.y && u.position.x > cell.position.x && u.position.x < unit.position.x).length === 0);
        }
        return (unit.position.x === cell.position.x && Math.abs(unit.position.y - cell.position.y) === 1) ||
               (unit.position.y === cell.position.y && Math.abs(unit.position.x - cell.position.x) === 1);
    }

    const isUnitAttackable = (attackedUnitId, attackingUnitId) => {
        const attackedUnit = state.units.find(u => u.id === attackedUnitId);
        const attackingUnit = state.units.find(u => u.id === attackingUnitId);
        if (!attackedUnit || !attackingUnit || attackedUnit.team === attackingUnit.team || !attackedUnit.isAlive) {
            return false;
        }
        const attackedUnitCell = state.board.find(c => c.position.x === attackedUnit.position.x && c.position.y === attackedUnit.position.y);
        if (!attackedUnitCell) {
            return false;
        }
        return isCellAvailable(attackedUnitCell.id, attackingUnit.id);
    }

    const handleUnitClick = (unitId) => {
        if (state.player !== state.currentPlayer) {
            return;
        }
        const unit = state.units.find(u => u.id === unitId);
        if (!unit || state.isFightInProgress) {
            return;
        }
        if (unit.team !== state.player && unit.isAlive && unit.isAttackable) { // attack
            const attackingUnit = state.units.find(u => u.id === state.selectedUnitId);
            if (!attackingUnit) {
                return;
            }
            state = {
                ...state,
                isFightInProgress: true,
                attackedUnitId: unit.id,
                units : state.units.map(u => u.id === state.selectedUnitId ? {
                    ...u,
                    position: {
                        x: unit.position.x + String(attackingUnit.position.x).localeCompare(unit.position.x),
                        y: unit.position.y + String(attackingUnit.position.y).localeCompare(unit.position.y)
                    }
                } : {
                    ...u,
                    isAttackable: false
                }),
                synced: false
            }
        } else if (unit.team === state.player && state.selectedUnitId === unit.id) { // deselect
            state = {
                ...state,
                selectedUnitId: null,
                board: state.board.map(c => ({
                    ...c,
                    isAvailable: false
                })),
                units: state.units.map(u => ({
                    ...u,
                    isAttackable: false
                }))
            };
        } else if (unit.team === state.player && state.selectedUnitId !== unit.id) { // select
            state = {
                ...state,
                selectedUnitId: payload.id,
                board: state.board.map(c => ({
                    ...c,
                    isAvailable: isCellAvailable(c.id, unit.id)
                })),
                units: state.units.map(u => ({
                    ...u,
                    isAttackable: isUnitAttackable(u.id, unit.id)
                }))
            };
        }
    }

    const moveUnitOnBoard = (unitId, cellId) => {
        const unit = state.units.find(u => u.id === unitId);
        const cell = state.board.find(c => c.id === cellId);
        if (!unit || !cell || !cell.isAvailable) {
            return;
        }
        state = {
            ...state,
            units: state.units.map(u => u.id === unit.id ? {
                ...u,
                position: cell.position
            } : {
                ...u,
                isAttackable: false
            }),
            board: state.board.map(c => ({
                ...c,
                isAvailable: false
            })),
            selectedUnitId: null
        };
    }

    const moveSelectedUnitOnBoard = (cellId) => {
        if (state.player !== state.currentPlayer) {
            return;
        }
        const unit = state.units.find(u => u.id === state.selectedUnitId);
        const cell = state.board.find(c => c.id === cellId);
        if (!unit || !cell || !cell.isAvailable || state.isFightInProgress) {
            return;
        }
        moveUnitOnBoard(state.selectedUnitId, cellId);
        state.currentPlayer = state.currentPlayer === Team.RED ? Team.BLUE : Team.RED;
        state.synced = false;
    }

    const checkGameOver = () => {
        const aliveUnits = state.units.filter(u => u.isAlive);
        if (!aliveUnits || aliveUnits.every(u => u.unit === Unit.FLAG || u.unit === Unit.BOMB)) {
            state.winner = Team.NONE; // tie
        } else if (!aliveUnits.find(u => u.team === Team.RED && u.unit === Unit.FLAG)) {
            state.winner = Team.BLUE;
        } else if (!aliveUnits.find(u => u.team === Team.BLUE && u.unit === Unit.FLAG)) {
            state.winner = Team.RED;
        }
    }

    const killUnit = (unitId) => {
        const unit = state.units.find(u => u.id === unitId);
        if (!unit || !unit.isAlive) {
            return;
        }
        const totalUnitsCount = state.units.filter(u => u.team === unit.team).length;
        const deadUnitsCount = state.units.filter(u => u.team === unit.team && !u.isAlive).length;
        state = {
            ...state,
            units: state.units.map(u => u.id === unitId ? {
                ...u,
                isAlive: false,
                isAttackable: false,
                position: {
                    x: deadUnitsCount % Math.ceil(totalUnitsCount / state.boardSize),
                    y: Math.floor(deadUnitsCount / Math.ceil(totalUnitsCount / state.boardSize))
                }
            } : u)
        }
    }

    const concludeFight = () => {
        const attackedUnit = state.units.find(u => u.id === state.attackedUnitId);
        const attackingUnit = state.units.find(u => u.id === state.selectedUnitId);
        if (!attackedUnit || !attackingUnit || !state.isFightInProgress) {
            return;
        }
        if ((attackedUnit.unit < attackingUnit.unit) ||
            (attackedUnit.unit === Unit.BOMB && attackingUnit.unit === Unit.MINER) ||
            (attackedUnit.unit === Unit.MARSHAL && attackingUnit.unit === Unit.SPY)) {
            const attackedUnitCell = state.board.find(c => c.position.x === attackedUnit.position.x && c.position.y === attackedUnit.position.y);
            killUnit(attackedUnit.id);
            moveUnitOnBoard(attackingUnit.id, attackedUnitCell.id);
        } else if (attackedUnit.unit > attackingUnit.unit) {
            killUnit(attackingUnit.id);
        } else {
            killUnit(attackedUnit.id);
            killUnit(attackingUnit.id);
        }
        checkGameOver();
        state = {
            ...state,
            units: state.units.map(u => ({
                ...u,
                isAttackable: false
            })),
            board: state.board.map(c => ({
                ...c,
                isAvailable: false
            })),
            isFightInProgress: false,
            selectedUnitId: null,
            attackedUnitId: null,
            currentPlayer: state.currentPlayer === Team.RED ? Team.BLUE : Team.RED,
            synced: false
        };
    }

    const setRoomId = (roomId) => {
        state = {
            ...state,
            roomId: roomId
        };
    }

    const setPlayer = (player) => {
        state = {
            ...state,
            player: player === 1 ? Team.RED : Team.BLUE
        };
    }

    const addUnits = (units) => {
        const maxUnitId = Math.max(...state.units.map(u => u.id), 1000);
        const maxGraveyardRedId = Math.max(...state.graveyardRed.map(c => c.id), 2000);
        const maxGraveyardBlueId = Math.max(...state.graveyardBlue.map(c => c.id), 3000);
        state = {
            ...state,
            units: state.units.concat(units.map((u, i) => ({
                ...u,
                id: maxUnitId + i + 1,
                isAttackable: false,
                isAlive: true
            }))),
            graveyardRed: state.graveyardRed.concat(units.filter(u => u.team === Team.RED).map((_, i) => ({
                id: maxGraveyardRedId + i + 1
            }))),
            graveyardBlue: state.graveyardBlue.concat(units.filter(u => u.team === Team.BLUE).map((_, i) => ({
                id: maxGraveyardBlueId + i + 1
            })))
        };
        state = {
            ...state,
            graveyardRed: state.graveyardRed.map((c, i) => ({
                ...c,
                position: {
                    x: i % Math.ceil(state.graveyardRed.length / state.boardSize),
                    y: Math.floor(i / Math.ceil(state.graveyardRed.length / state.boardSize))
                }
            })),
            graveyardBlue: state.graveyardBlue.map((c, i) => ({
                ...c,
                position: {
                    x: i % Math.ceil(state.graveyardBlue.length / state.boardSize),
                    y: Math.floor(i / Math.ceil(state.graveyardBlue.length / state.boardSize))
                }
            }))
        };
    }

    const updateState = (newState) => {
        if (!state.currentPlayer) {
            const enemyUnits = newState.units.filter(u => u.team !== state.player);
            addUnits(enemyUnits);
            if (enemyUnits.length) {
                state = {
                    ...state,
                    currentPlayer: Team.RED
                };
            }
        } else {
            state = {
                ...newState,
                player: state.player,
                synced: true
            };
        }
    }

    if (state.winner) {
        return state;
    }

    const { type, payload } = action;

    switch (type) {
        case GAME_STATE_NEW_GAME:
            setNewGameState(payload.boardSize, payload.units);
            break;
        case GAME_STATE_MOVE_SELECTED_UNIT_ON_BOARD:
            moveSelectedUnitOnBoard(payload.id);
            break;
        case GAME_STATE_HANDLE_UNIT_CLICK:
            handleUnitClick(payload.id);
            break;
        case GAME_STATE_CONCLUDE_FIGHT:
            concludeFight();
            break;
        case GAME_STATE_SET_ROOM_ID:
            setNewGameState(exampleGameState.boardSize, []);
            setRoomId(payload.id);
            break;
        case GAME_STATE_SET_PLAYER:
            setPlayer(payload.player);
            break;
        case GAME_STATE_ADD_UNITS:
            addUnits(payload.units);
            break;
        case GAME_STATE_UPDATE_STATE:
            updateState(payload.state);
            break;
        default:
            break;
    }

    return state;
};
