import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";

import {
    gameStateMoveSelectedUnitOnBoard,
    gameStateHandleUnitClick,
    gameStateConcludeFight
} from "../../state/gameState/actions";
import { getGameState } from "../../state/gameState/selectors";
import { Page } from "../../enums/Page";
import { Board } from "./Board";
import { syncState, leaveRoom } from "../../state/messages/actions";

export const Game = ({ onPageChange }) => {
    const dispatch = useDispatch();
    const gameState = useSelector(getGameState);

    useEffect(() => {
        if (!gameState.synced) {
            dispatch(syncState(gameState));
        }
        if (gameState.isFightInProgress) {
            setTimeout(() => {
                dispatch(gameStateConcludeFight());
            }, 3000);
        }
        if (gameState.winner) {
            dispatch(leaveRoom(gameState.roomId));
        }
    });

    return (
        <div className="flex h-screen bg-gray-800">
            <div className="flex flex-col m-auto items-center">
                <Board
                    gameState={gameState}
                    onUnitClick={(unitId) => dispatch(gameStateHandleUnitClick(unitId))}
                    onBoardClick={(cellId) => dispatch(gameStateMoveSelectedUnitOnBoard(cellId))}
                    onMenuButtonClick={() => onPageChange(Page.MAIN_PAGE)}
                />
            </div>
        </div>
    );
};

Game.propTypes = {
    onPageChange: PropTypes.func.isRequired
}
