export const PREPARE_STATE_ACTIVATE_UNIT = "PREPARE_STATE_ACTIVATE_UNIT";
export const PREPARE_STATE_MOVE_SELECTED_UNIT_ON_BOARD = "PREPARE_STATE_MOVE_SELECTED_UNIT_ON_BOARD";
export const PREPARE_STATE_MOVE_SELECTED_UNIT_ON_HAND = "PREPARE_STATE_MOVE_SELECTED_UNIT_ON_HAND";
export const PREPARE_STATE_NEW_GAME = "PREPARE_STATE_NEW_GAME";

export const prepareStateActivateUnit = (unitId) => ({
    type: PREPARE_STATE_ACTIVATE_UNIT,
    payload: { id: unitId }
});

export const prepareStateMoveSelectedUnitOnBoard = (boardCellId) => ({
    type: PREPARE_STATE_MOVE_SELECTED_UNIT_ON_BOARD,
    payload: { id: boardCellId }
});

export const prepareStateMoveSelectedUnitOnHand = (handCellId) => ({
    type: PREPARE_STATE_MOVE_SELECTED_UNIT_ON_HAND,
    payload: { id: handCellId }
});

export const prepareStateNewGame = (player) => ({
    type: PREPARE_STATE_NEW_GAME,
    payload: { player: player }
});
